<template>
    <div class="row">
        <div class="col-xxl-6 hidden-xl hidden-lg hidden-md hidden-sm hidden-xs hidden-zero">
            <section-block title="Владелец" class="hidden-zero hidden-xs hidden-sm">
                <div class="profile-block">
                    <img src="../../assets/images/avatar.png" alt="" class="img-circle">
                    <div class="fullname">{{seller.firstname}} {{seller.lastname}}</div>
                    <div class="rating"><el-rate
                            v-model="seller.rating"
                            disabled
                            text-color="#ff9900">
                    </el-rate></div>
                </div>
                <div class="block-lines">
                    <div class="block-line">
                        Зарегистрирован <span class="pull-right">{{seller.since}}</span>
                    </div>
                    <div class="block-line">
                        Группа <span class="pull-right">{{seller.group}}</span>
                    </div>
                </div>
                <router-link :to="'/chat/'+seller.id" v-if="auth.id != seller.id">
                    <button class="btn btn-special-primary">Отправить сообщение</button>
                </router-link>
            </section-block>
        </div>
        <div class="col-xxl-12 col-xl-16 col-lg-16 col-md-14 col-sm-24">
            <section-block title="Информация о сделке">
                <div class="good-line">
                    <router-link :to="'/goods/' + good.id">
                        <div class="preview">
                            <img src="../../assets/images/good.png" alt="Good"><br>
                            <div class="statuses hidden-sm hidden-md hidden-lg hidden-xl hidden-xxl">
                                <el-tooltip content="Бывший в употреблении" placement="top" v-if="good.used">
                                    <div class="status used"><i class="fa fa-history"></i></div>
                                </el-tooltip>
                                <el-tooltip :content="'Оценка эксперта: ' + good.evaluation + ' ₽'" placement="top" v-if="good.evaluation">
                                    <div class="status evaluation"><i class="fa fa-users"></i></div>
                                </el-tooltip>
                            </div>
                        </div>
                        <div class="good-content">
                            <div class="statuses hidden-xs">
                                <el-tooltip content="Бывший в употреблении" placement="top" v-if="good.used">
                                    <div class="status used"><i class="fa fa-history"></i></div>
                                </el-tooltip>
                                <el-tooltip :content="'Оценка эксперта: ' + good.evaluation + ' ₽'" placement="top" v-if="good.evaluation">
                                    <div class="status evaluation"><i class="fa fa-users"></i></div>
                                </el-tooltip>
                            </div>
                            <!--<div class="price hidden-sm hidden-md hidden-lg hidden-xl hidden-xxl"><span>{{good.price}}</span> ₽</div>-->
                            <div class="good-title">{{good.name}}</div>
                            <div class="good-category">{{good.category}}</div>
                            <div class="good-description">
                                <div class="listed">
                                    <b class="hidden-xs">Тип сделки:</b> {{deal.type}}
                                </div>
                                <div class="listed">
                                    <b class="hidden-xs">Сделка создана:</b> {{deal.date}}
                                </div>
                            </div>
                            <div class="actions">
                                <div class="price"><span>{{deal.cost}}</span> ₽</div>
                            </div>
                        </div>
                    </router-link>
                </div>
                <div class="states">
                    <div class="state initial">
                        <i class="fa fa-exclamation"></i>
                        Это <u>новая</u> сделка, вы можете отменить эту сделку в любой момент.
                    </div>
                    <div class="state inprogress">
                        <i class="fa fa-exclamation"></i>
                        Сделка <u>в процессе</u>, сделка может быть отменена только владельцем товара и только до подтверждения его передачи.
                    </div>
                    <div class="state finished">
                        <i class="fa fa-exclamation"></i>
                        Сделка <u>успешно завершена</u>.
                    </div>
                    <div class="state canceled">
                        <i class="fa fa-exclamation"></i>
                        Сделка была <u>отменена</u>, все текущие данные по ней были заморожены.
                    </div>
                </div>
            </section-block>
            <section-block title="Действия">
                <div class="actions">
                    <div class="action-line cancel row">
                        <span class="col-lg-18 col-md-24 col-sm-16">На этом этапе Вы можете отменить сделку</span>
                        <button class="btn btn-danger col-lg-6 col-md-24 col-sm-8">Отменить сделку</button>
                    </div>
                    <div class="action-line">
                        <div class="delivery">
                            <div class="form-line">
                                <span class="input" :class="{'input-filled': delivery.address}">
                                    <input class="input-field" type="text" v-model="delivery.address"/>
                                    <label class="input-label">
                                        <span class="input-label-content">Адрес доставки</span>
                                    </label>
                                </span>
                            </div>
                            <div class="form-line">
                                <span class="input" :class="{'input-filled': delivery.date}">
                                    <mask-input class="input-field" type="text" v-model="delivery.date"
                                                :mask="[/[0-3]/, /\d/, ' ', '/', ' ', /[0-1]/, /\d/, ' ', '/', ' ', /[1-2]/, /[9,0]/, /\d/, /\d/, ' ', /[0-2]/, /\d/, ':', /[0-5]/, /\d/]"
                                                :guide="true"
                                                placeholderChar="_"
                                                placeholder="__ / __ / ____ __:__"></mask-input>
                                    <label class="input-label">
                                        <span class="input-label-content">Дата и время доставки</span>
                                    </label>
                                </span>
                            </div>
                            <button class="btn btn-success">Заказать доставку</button>
                        </div>

                    </div>
                </div>
            </section-block>
            <section-block title="Чат сделки">
                <div class="chat">
                    <table class="dialog-view">
                        <tr class="messages">
                            <td class="acenter">
                                <div class="scroller messages_scroll" style="height: 300px;">
                                    <div class="scroller_content messages_content list" v-chat-scroll>
                                        <template v-if="chat.messages.length">
                                            <div class="message" :key="key" :class="[mess.from == auth.id ? 'aright' : 'aleft']" v-for="(mess, key) in chat.messages">
                                                <div class="mess-time" v-if="mess.from == auth.id">{{mess.date}}</div>
                                                <div class="bubble" :class="[mess.from == auth.id ? 'message-from' : 'message-to']">{{mess.content}}</div>
                                                <div class="mess-time" v-if="mess.from != auth.id">{{mess.date}}</div>
                                            </div>
                                        </template>
                                        <span v-else class="noData">Выберите диалог</span>
                                    </div>
                                    <div class="scroller_track">
                                        <div class="scroller_free">
                                            <div class="scroller_bar messages_bar"></div>
                                        </div>
                                    </div>
                                </div>
                            </td>
                        </tr>
                        <tr class="input-text" v-show="deal.state != 'canceled'">
                            <td>
                                <textarea rows="3" placeholder="Ваше сообщение..." @keyup.ctrl.enter.prevent="send" v-model="message"></textarea>
                                <button class="send" @click="send"><i class="fa fa-send"></i></button>
                            </td>
                        </tr>
                    </table>
                </div>
            </section-block>
        </div>
        <div class="col-xxl-6 col-xl-8 col-lg-8 col-md-10 col-sm-24">
            <section-block title="Покупатель">
                <div class="profile-block">
                    <img src="../../assets/images/avatarDF.png" alt="" class="img-circle">
                    <div class="fullname">{{purchaser.firstname}} {{purchaser.lastname}}</div>
                    <div class="rating"><el-rate
                            v-model="purchaser.rating"
                            disabled
                            text-color="#ff9900">
                    </el-rate></div>
                </div>
                <div class="block-lines">
                    <div class="block-line">
                        Зарегистрирован <span class="pull-right">{{purchaser.since}}</span>
                    </div>
                    <div class="block-line">
                        Группа <span class="pull-right">{{purchaser.group}}</span>
                    </div>
                </div>
                <router-link :to="'/chat/'+purchaser.id" v-if="auth.id != purchaser.id">
                    <button class="btn btn-special-primary">Отправить сообщение</button>
                </router-link>
                <button class="btn btn-danger btn-full" style="margin-top: 10px">Пожаловаться</button>
            </section-block>
        </div>
    </div>
</template>

<script>
    import sectionBlock from '../global/section.vue'
    import $ from 'jquery'
    import baron from '../global/baron/baron.min'
    import { mapState, mapGetters } from 'vuex'
    import maskInput from 'vue-text-mask'

    export default {
        name: 'dealPage',
        components: {
            sectionBlock,
            maskInput
        },
        data() {
            return {
                delivery: {
                    address: '',
                    date: ''
                },
                scroll_messages: {},
                message: '',
                chat: {
                    messages: [
                        {
                            content: 'Тестирование сообщений на странице сделки',
                            from: 1,
                            read: false,
                            date: '18:30'
                        },
                        {
                            content: 'Тестирование сообщений на странице сделки',
                            from: 2,
                            read: false,
                            date: '18:30'
                        },
                        {
                            content: 'Тестирование сообщений на странице сделки',
                            from: 1,
                            read: false,
                            date: '18:30'
                        },
                        {
                            content: 'Тестирование сообщений на странице сделки',
                            from: 2,
                            read: false,
                            date: '18:30'
                        },
                        {
                            content: 'Тестирование сообщений на странице сделки',
                            from: 1,
                            read: false,
                            date: '18:30'
                        },
                        {
                            content: 'Тестирование сообщений на странице сделки',
                            from: 2,
                            read: false,
                            date: '18:30'
                        },
                        {
                            content: 'Тестирование сообщений на странице сделки',
                            from: 1,
                            read: false,
                            date: '18:30'
                        },
                        {
                            content: 'Тестирование сообщений на странице сделки',
                            from: 2,
                            read: false,
                            date: '18:30'
                        },
                    ]
                },
                deal: {
                    id: 1,
                    cost: 200,
                    type: 'продажа',
                    date: '14 апреля 2017',
                    state: 'initial'
                },
                good: {
                    id: 1,
                        name: 'Название товара #1',
                        category: 'Категория #1',
                        used: true,
                        price: 200,
                        dealType: 'продажа',
                        evaluation: '100,000 - 200,000',
                        date: '6 февраля 2017',
                },
                seller: {
                    id: 1,
                    firstname: 'Евгений',
                    lastname: 'Горбатов',
                    rating: 3.5,
                    since: '26 февраля 2016',
                    group: 'проверенный участник'
                },
                purchaser: {
                    id: 2,
                    firstname: 'Дмитрий',
                    lastname: 'Федоров',
                    rating: 2.5,
                    since: '12 мая 2016',
                    group: 'новый участник'
                }
            }
        },
        computed: {
            ...mapState(['auth']),
//            ...mapGetters(['fullname']),
        },
        methods: {
            send() {
                if (this.message.trim()) {
                    let time = new Date();
                    this.chat.messages.push({
                        date: ('0'+time.getHours()).slice(-2) + ':' + ('0'+time.getMinutes()).slice(-2),
                        read: false,
                        content: this.message.replace(/\n/g, '<br>'),
                        from: this.auth.id
                    });
                    this.message = '';
                }
            }
        },
        mounted() {
            this.scroll_messages = baron({
                root: '.messages_scroll',
                scroller: '.messages_content',
                bar: '.messages_bar',
                $: $
            });
        }
    }
</script>

<style lang="sass" src="@/assets/sass/pages/goods.sass"></style>
<style lang="sass" src="@/assets/sass/pages/deal.sass"></style>


